* {
  box-sizing: border-box;
}

.wb-body {
  background-color: #1e2120 !important;
  color: white;
  padding: 1rem;
}

::-webkit-scrollbar {
  width: 9px;
  height: 5px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(266deg, #607d8b 5%, #121213 64%);
  border-radius: 0px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(360deg, #f9d4ff 100%, #c7ceff 100%);
}

::-webkit-scrollbar-track {
  background: #ffffff;
  border-radius: 0;
  box-shadow: inset 1px 30px 30px #f0f0f0;
}

.container-fluid,
.jumbotron {
  height: 100vh !important;
  // background-image: url('../images/linux.jpg');
  // background-position: center;
  position: absolute !important;
}

img.pb-5.pt-4 {
  height: 40vh;
  width: 100%;
  object-fit: cover;
  opacity: 0.5;
}

.bg-custom {
  background-color: #07252559 !important;
  border-radius: 4px;
}

.lead {
  font-size: 1.0rem !important;
  font-weight: 300;
}

.fa-folder:hover{
  color:#13bf34 !important
}

canvas{
  position: fixed !important;
}

